import { NgModule } from '@angular/core';
import { TranslateModule } from '@ngx-translate/core';
import { IonicPageModule } from 'ionic-angular';

import { TravelOptionsPage } from './travelOptions';

@NgModule({
  declarations: [
    TravelOptionsPage,
  ],
  imports: [
    IonicPageModule.forChild(TravelOptionsPage),
    TranslateModule.forChild()
  ],
  exports: [
    TravelOptionsPage
  ]
})
export class TravelOptionsPageModule { }



